import React from 'react';
import { connect } from 'react-redux';
import { follow, setCurrentPages, unfollow,toggleFollowingProgress,getUsers } from '../Redux/users-reducer';
import Users from './Users';
import Preloader from '../common/preloader/preloader.jsx';
import { getCurrentPage, getFollowingInProgress, getIsFetching, getPageSize, getTotalUsersCount, takeUsers } from '../Redux/users-selectors';

class UsersContainer extends React.Component {

    componentDidMount() {
        this.props.getUsers(this.props.currentPage,this.props.pageSize);
    }

    onPageChanged = (pageNumber) => {
        this.props.setCurrentPages(pageNumber);
        this.props.getUsers(pageNumber,this.props.pageSize);
    }
    
    render() {
        return <>
        {this.props.isFetching ? <Preloader /> : null}
            <Users totalUsersCount={this.props.totalUsersCount}
                pageSize={this.props.pageSize}
                currentPage={this.props.currentPage}
                onPageChanged={this.onPageChanged}
                users={this.props.users}
                unfollow={this.props.unfollow}
                follow={this.props.follow}
                followingInProgress={this.props.followingInProgress}
            />
                    </>
    }
}

const mapStateToProps = (state) => {
    return {
        users: takeUsers(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        followingInProgress:getFollowingInProgress(state)
    }
}

export default connect(mapStateToProps, 
{ follow,unfollow, setCurrentPages, toggleFollowingProgress, getUsers})(UsersContainer);
